.caraouseltext{
  padding: 10px 40px;
  height: 120px;
}
.price-bodyhover:hover .upbuttonhover{
  color: #fff!important;
  background-color:var(--orange-background);
  transition-duration:0.7s;
  cursor: pointer;
}
.slides{
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:center;
  position: relative;
  /*margin-top: 40px;*/
}
.slide{
  height:250px;
  transform-origin:center center;
  transform:scale(1) translate(0px,0px) perspective( 750px ) rotateY(0deg);
  transition-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
  transition-duration: 0.5s;
  position:absolute;
  top:0;
  box-shadow:0 30px 50px rgba(0,0,0,0.1);
  border: 5px solid #545151;
  background-color: #fff;
  top: 25px;
  padding: 15px 15px;
  text-align: center;
}
.slide * {
  pointer-events: none;
}
.slide--left {
  transform:scale(0.85) translate(-100%,0px) perspective( 750px ) rotateY(0deg);
  box-shadow:0 15px 25px rgba(0,0,0,0.1);
  z-index:2;
}
.slide--left-left {
  transform:scale(0.75) translate(-190%,0px) perspective( 750px ) rotateY(0deg);
  box-shadow:0 15px 25px rgba(0,0,0,0.1);
  z-index:1;
}
.slide{
  border: 5px solid #f5f5f5;
  padding: 10px 10px;
  height: 250px;
  width: 250px;
}
.slide div{
  height: 220px;
  width: 220px;
  background-size: 100% 100%;
}
.slide--center {
  transform:scale(1) translate(0px, 0px) perspective( 750px ) rotateY(0deg);
  box-shadow:0 30px 50px rgba(0,0,0,0.1);
  z-index:3;
}
.slide--right {
  transform:scale(0.85) translate(100%,0px) perspective( 750px ) rotateY(0deg);
  box-shadow:0 15px 25px rgba(0,0,0,0.1);
  z-index:2;
}
.slide--right-right {
  transform:scale(0.75) translate(190%,0px) perspective( 750px ) rotateY(0deg);
  box-shadow:0 15px 25px rgba(0,0,0,0.1);
  z-index:1;
}
.slide__icon {
  height:100%;
  background:rgba(255,255,255,0.5);
  position:relative;
  display:flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.slide__icon:before {
  content:attr(data-icon);
  font-size:3rem;
  position:absolute;
  display:flex;
  align-items: center;
  justify-content: center;
  height:100px;
  border-radius: 50px;
  background:rgba(255,255,255,1);
}
.slide__detail{
  flex:1;
  display:flex;
  align-items: center;
  justify-content: center;
}
.caraouselText{
  font-size: 12px;
  text-align: center;
  margin-top: 10px;
  font-weight: 600;
  text-transform: capitalize;
  color: #333;
  background: #ffffffad;
  position: absolute;
  padding-top: 6px;
  padding-bottom: 6px;
  bottom: 6px;
  white-space: nowrap;
  right: 0px;
  display: none;
  width: 100%;
}
.slide--center div span{
  display: block;
}
@media only screen and (max-width : 736px){
  .slide--center div span{
    display: none;
  }
  .caraouselblock{
    padding: 0px 20px !important;
    height: 140px !important;
    margin-bottom: 0px !important;
  }
  .slide{
    height: 94px;
    width: 94px;
  }
  .slide div {
      height: 65px;
      width: 65px;
      background-size: 100% 100%;
  }
}
@media only screen and (min-width : 1200px){
  .caraouselblock{
    padding: 0px 20px !important;
    height: 320px !important;
    margin-bottom: 0px !important;
  }
}
@media screen and (max-width: 1160px) and (min-width: 737px){
  .slide--center div span{
    display: none;
  }
  .slide{
    padding: 5px 5px;
    height: 150px;
    width: 150px;
  }
  .slide div {
      height: 130px;
      width: 130px;
      background-size: 100% 100%;
  }
}
@media screen and (max-width: 440px) and (min-width: 200px){
  .slide--center div span{
    display: none;
  }
  .slide{
    padding: 5px 5px;
    height: 75px;
    width: 75px;
  }
  .slide div {
      height: 55px;
      width: 55px;
      background-size: 100% 100%;
  }
}
.caraouselcontentheader h1{
font-size: 41px;
}
.caraouselcontentheader hr{
  height: 2px;
  border: 1px solid var(--orange-background);
  background-color: var(--orange-background);
}
.caraouselfoot{
  pointer-events: auto!important;
  cursor: pointer;
}
.caraouselblock{
  background-size: 100% 100%;
  /* background-image: url("/images/banner1.jpg"); */
  /* background: #f5f5f561; */
  height: 220px;
  padding: 0px 50px;
  /*margin-bottom: 60px;*/
}
